<script>

	var textsMaxWidth = 10
	function updateUi(){
		document.documentElement.style.setProperty('--textsMaxWidth', textsMaxWidth + "px")
	}
	var start = setInterval(starting,1);
	function starting(){
		if (textsMaxWidth < 1000){
			textsMaxWidth++
			updateUi()
		}else{
			clearInterval(start);
		}
	}

</script>
